
.paginator {
    color: darkblue;
    margin: 0.5rem 0 0 18rem;

}

.selectedPage {
    font-weight: bold;
    color: #ffffff;
}

.pageNumber  {
    border-radius: 5px;
    padding: 3px;
    border: 1px solid grey;

}

.pageNumber.selectedPage {

    font-weight: bold;
    border-color: #ff0000;
    background-color: #ff3b3b;
}
.paginator Button {
    margin:0 0 0.3rem 0;
    padding: 0.39rem 0.36rem 0.37rem 0.36rem;
}



